import { Injectable } from '@angular/core';
import { HttpResponse } from '@angular/common/http';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot, Routes } from '@angular/router';
import { UserRouteAccessService } from '../core';
import { Observable, of } from 'rxjs';
import { filter, map } from 'rxjs/operators';
import { SubjectService } from './subject.service';
import { ISubject, Subject } from '../shared/model/content/subject.model';
import { SubjectPublishComponent } from './subject-update-publish.component';
import { SubjectManageComponent } from './subject-manage.component';
import { SubjectDeleteComponent } from './subject-delete.component';
import { SubjectUnpublishComponent } from './subject-update-unpublish.component';
import { SubjectCreateComponent } from './subject-create.component';
import { SubjectUpdateComponent } from './subject-update.component';
import { SubjectDetailsComponent } from './subject-details.component';

@Injectable({ providedIn: 'root' })
export class SubjectResolve implements Resolve<ISubject> {
    constructor(private service: SubjectService) { }

    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<ISubject> {
        const id = route.params['id'] ? route.params['id'] : null;
        if (id) {
            return this.service.getSubjectBySubjectId(id).pipe(
                filter((response: HttpResponse<ISubject>) => response.ok),
                map((subject: HttpResponse<ISubject>) => subject.body)
            );
        }
        return of(new Subject());
    }
}

@Injectable({ providedIn: 'root' })
export class SubjectWithTreeResolve implements Resolve<any> {
    constructor(private service: SubjectService) { }

    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> {
        const id = route.params['id'] ? route.params['id'] : null;
        if (id) {
            return this.service.getSubjectTreeBySubjectId(id).pipe(
                filter((response: HttpResponse<any>) => response.ok),
                map((subject: HttpResponse<any>) => subject.body)
            );
        }
        return of(new Subject());
    }
}

export const subjectRoute: Routes = [
    {
        path: '',
        component: SubjectManageComponent,
        data: {
            authorities: ['ROLE_USER'],
            pageTitle: 'Manage subjects'
        },
        canActivate: [UserRouteAccessService]
    },
    {
        path: 'create',
        component: SubjectCreateComponent,
        data: {
            authorities: ['ROLE_USER'],
            pageTitle: 'Create new subject'
        },
        canActivate: [UserRouteAccessService]
    },
    {
        path: ':id/edit',
        component: SubjectUpdateComponent,
        resolve: {
            subject: SubjectWithTreeResolve
        },
        data: {
            authorities: ['ROLE_USER'],
            pageTitle: 'Edit subject',
            action: 'edit'
        },
        canActivate: [UserRouteAccessService]
    },
    {
        path: ':id/view',
        component: SubjectDetailsComponent,
        resolve: {
            subject: SubjectWithTreeResolve
        },
        data: {
            authorities: ['ROLE_USER'],
            pageTitle: 'View subject'
        },
        canActivate: [UserRouteAccessService]
    },
    {
        path: ':id/delete',
        component: SubjectDeleteComponent,
        resolve: {
            subject: SubjectResolve
        },
        data: {
            authorities: ['ROLE_USER'],
            pageTitle: 'Delete subject'
        },
        canActivate: [UserRouteAccessService]
    },
    {
        path: ':id/publish',
        component: SubjectPublishComponent,
        resolve: {
            subject: SubjectResolve
        },
        data: {
            authorities: ['ROLE_USER'],
            pageTitle: 'Publish subject',
            action: 'publish'
        },
        canActivate: [UserRouteAccessService]
    },
    {
        path: ':id/unpublish',
        component: SubjectUnpublishComponent,
        resolve: {
            subject: SubjectResolve
        },
        data: {
            authorities: ['ROLE_USER'],
            pageTitle: 'Unpublish subject',
            action: 'unpublish'
        },
        canActivate: [UserRouteAccessService]
    }
];
