import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { RouterModule } from '@angular/router';
import { NgJhipsterModule } from 'ng-jhipster';
import { InfiniteScrollModule } from 'ngx-infinite-scroll';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { SharedModule } from '../shared';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { subjectRoute } from './subject.route';
import { SubjectManageComponent } from './subject-manage.component';
import { SubjectPublishComponent } from './subject-update-publish.component';
import { SubjectDeleteComponent } from './subject-delete.component';
import { SubjectUnpublishComponent } from './subject-update-unpublish.component';
import { SubjectCreateComponent } from './subject-create.component';
import { SubjectUpdateComponent } from './subject-update.component';
import { SubjectDetailsComponent } from './subject-details.component';

@NgModule({
  imports: [
    RouterModule.forChild(subjectRoute),
    FormsModule,
    ReactiveFormsModule,
    SharedModule,
    NgJhipsterModule,
    InfiniteScrollModule,
    NgbModule
  ],
  declarations: [
    SubjectManageComponent,
    SubjectCreateComponent,
    SubjectUpdateComponent,
    SubjectDetailsComponent,
    SubjectDeleteComponent,
    SubjectPublishComponent,
    SubjectUnpublishComponent
  ],
  schemas: [CUSTOM_ELEMENTS_SCHEMA]
})

export class SubjectModule {
}