import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { INACTIVE } from '../shared/constants/common.constants'
import { HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { PluginService } from '../shared/plugin/plugin.service';
import { SubjectService } from './subject.service';
import { ISubject } from '../shared/model/content/subject.model';

@Component({
    template: "",
    providers: [SubjectService]
})
export class SubjectUnpublishComponent implements OnInit {
    constructor(
        public router: Router,
        public activatedRoute: ActivatedRoute,
        public subjectService: SubjectService,
        protected pluginService: PluginService
    ) { }

    ngOnInit() {
        this.activatedRoute.data.subscribe(({ subject }) => {
            this.unpublishGeneralSubject(subject);
        });
    }

    protected subscribeToSaveResponse(result: Observable<HttpResponse<ISubject>>) {
        result.subscribe((res: HttpResponse<ISubject>) => this.onSaveSuccess(res), (errRes: HttpErrorResponse) => this.onSaveError(errRes));
    }

    unpublishGeneralSubject(subject) {
        subject.status = INACTIVE;
        this.subscribeToSaveResponse(this.subjectService.updateSubjectStatus(subject));
        this.router.navigateByUrl('/subject');
    }


    onSaveSuccess(resp: HttpResponse<ISubject>) {
        if (resp.body.status === INACTIVE) {
            this.pluginService.alertMessage("Subject unpublished successfully", "success")
        }
    }

    onSaveError(errRes: HttpErrorResponse) {
        this.pluginService.alertMessage("Could not unpublish, please contact admin", "error")
    }

}