import { Component, OnInit, OnDestroy } from "@angular/core";
import { Router, ActivatedRoute } from '@angular/router';
import { JhiEventManager, JhiParseLinks } from 'ng-jhipster';
import { Subscription } from 'rxjs';
import { Principal } from '../core';
import { ITEMS_PER_PAGE } from '../shared';
import { HttpResponse, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { PluginService } from '../shared/plugin/plugin.service';
import { SubjectService } from './subject.service';
import { ISubject } from '../shared/model/content/subject.model';
import { ICurriculum } from '../shared/model/content/curriculum.model';
import { IGrade } from '../shared/model/content/grade.model';
import { CurriculumService } from '../curriculum/curriculum.service';

declare var $: any;
declare var swal: any;

@Component({
    templateUrl: "./subject-manage.component.html",
    providers: [SubjectService]
})
export class SubjectManageComponent implements OnInit, OnDestroy {
    subjects: ISubject[];
    curriculumList: ICurriculum[] = [];
    gradeList: IGrade[] = [];
    eventSubscriber: Subscription;
    totalItems: number;
    itemsPerPage: number;
    totalPages: number;
    page: any;
    predicate: any;
    reverse: any;
    links: any;
    currentAccount: any;
    selectedCurriculum: any;
    selectedGrade: any;

    constructor(
        public _router: Router,
        public activatedRoute: ActivatedRoute,
        public curriculumService: CurriculumService,
        public subjectService: SubjectService,
        protected eventManager: JhiEventManager,
        protected parseLinks: JhiParseLinks,
        protected principalService: Principal,
        public pluginService: PluginService
    ) {
        this.subjects = [];
        this.itemsPerPage = ITEMS_PER_PAGE;
        this.page = 0;
        this.links = {
            last: 0
        };
        this.predicate = 'id';
        this.reverse = false;
    }

    ngOnInit() {
        this.principalService.identity().then(account => {
            this.currentAccount = account;
        });
        this.registerChangeInSubjects();
        this.loadCurriculums();
    }

    reset() {
        this.page = 0;
        this.subjects = [];
        this.loadSubjectList();
    }

    loadPage(page) {
        this.page = page;
        this.loadSubjectList();
    }

    loadCurriculums() {
        this.curriculumService.getCurriculumSearch().subscribe(
            (res: HttpResponse<ICurriculum[]>) => this.assignCurriculumList(res.body),
            (res: HttpErrorResponse) => this.onError(res.message)
        )
    }

    assignCurriculumList(data: ICurriculum[]) {
        this.curriculumList = [];
        for (let i = 0; i < data.length; i++) {
            this.curriculumList.push(data[i]);
        }
    }

    trackId(index: number, item: ISubject) {
        return item.id;
    }

    sort() {
        const result = [this.predicate + ',' + (this.reverse ? 'asc' : 'desc')];
        if (this.predicate !== 'id') {
            result.push('id');
        }
        return result;
    }

    registerChangeInSubjects() {
        this.eventSubscriber = this.eventManager.subscribe('subjectListModification', response => this.reset());
    }

    protected onError(errorMessage: string) {
        this.pluginService.alertMessage(errorMessage, "error");
    }

    onCurriculumChange() {
        let gradesApplicableFrom, gradesApplicableTo;
        if (this.selectedCurriculum) {
            gradesApplicableFrom = this.selectedCurriculum.gradesApplicableFrom;
            gradesApplicableTo = this.selectedCurriculum.gradesApplicableTo;
            this.generateGradeList(gradesApplicableFrom, gradesApplicableTo);
            this.selectedGrade = null;
        }
    }

    generateGradeList(min, max) {
        this.gradeList = [];
        for (var i = min; i <= max; i++) {
            this.gradeList.push({
                "id": i,
                "name": i
            });
        }
        return this.gradeList;
    }

    loadSubjectList() {
        this.subjects = [];
        if (this.selectedCurriculum && this.selectedGrade) {
            this.subjectService
                .getSubjectByCurriculumIdAndGradeId(this.selectedCurriculum.id, this.selectedGrade.id, null)
                .subscribe(
                    (res: HttpResponse<ISubject[]>) => this.paginateSubjects(res.body, res.headers),
                    (res: HttpErrorResponse) => this.onError(res.message)
                );
        } else {
            this.pluginService.alertMessage("Please select a filter and then apply", "error")
        }
    }

    protected paginateSubjects(data: ISubject[], headers: HttpHeaders) {
        //this.links = this.parseLinks.parse(headers.get('link'));
        this.totalItems = parseInt(headers.get('X-Total-Count'), 10);
        if (data.length > 0) {
            for (let i = 0; i < data.length; i++) {
                this.subjects.push(data[i]);
            }
        } else {
            this.pluginService.alertMessage("No data found..!", "warning");
        }

    }

    ngOnDestroy() {
        this.eventManager.destroy(this.eventSubscriber);
    }
}





