import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { PluginService } from '../shared/plugin/plugin.service';
import { JhiEventManager } from 'ng-jhipster';
import { SubjectService } from './subject.service';

declare var $: any;
declare var swal: any;

@Component({
    template: "",
    providers: [SubjectService]
})
export class SubjectDeleteComponent implements OnInit {
    constructor(
        public router: Router,
        public activatedRoute: ActivatedRoute,
        protected eventManager: JhiEventManager,
        public subjectService: SubjectService,
        protected pluginService: PluginService
    ) { }

    ngOnInit() {
        this.activatedRoute.data.subscribe(({ subject }) => {
            this.deletegeneralSubject(subject);
        });
    }

    protected subscribeToDeleteResponse(resp: Observable<HttpResponse<any>>) {
        resp.subscribe((res: HttpResponse<any>) => this.onDeleteSuccess(res), (res: HttpErrorResponse) => this.onDeleteError(res));
    }

    deletegeneralSubject(subject) {
        var localthisObject = this;
        swal({
            title: "Are you sure to delete this subject ?",
            type: "warning",
            showCancelButton: true,
            confirmButtonText: "Yes",
            confirmButtonColor: "#32b432",
            cancelButtonText: "No",
            closeOnConfirm: false,
            closeOnCancel: true
        },
            function (isConfirm) {
                if (isConfirm) {
                    localthisObject.confirmDelete(subject.id);
                } else {
                    localthisObject.previousUrl();
                }
            });
    }

    confirmDelete(id: number) {
        this.subscribeToDeleteResponse(this.subjectService.delete(id));
    }

    previousUrl() {
        this.router.navigateByUrl('/subject');
    }

    protected onDeleteSuccess(resp) {
        this.pluginService.alertMessage("Subject deleted successfully", "success");
        this.previousUrl();
    }

    onDeleteError(err: HttpErrorResponse) {
        this.pluginService.alertMessage("Could not delete general subject, please contact admin", "error")
    }

}