import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../environments/environment';
import { ISubject } from '../shared/model/content/subject.model';
import { ACTIVE } from '../shared/constants/common.constants';

type EntityResponseType = HttpResponse<ISubject>;
type EntityArrayResponseType = HttpResponse<ISubject[]>;

let headers: HttpHeaders = new HttpHeaders();
headers = headers.append('Access-Control-Allow-Origin', '*').append('Access-Control-Allow-Headers', '*');

@Injectable({ providedIn: 'root' })
export class SubjectService {

    public subjectResourceUrl = environment.API_URL + 'services/learning/api/curriculum-subjects';
    public subjectResourceUrlWithTree = environment.API_URL + 'services/learning/api/curriculum-subjects/subject-tree';
    public subjectResourceUrlSearch = environment.API_URL + 'services/learning/api/curriculum-subjects/search';

    constructor(protected http: HttpClient) { }

    getSubjectBySubjectId(id: number): Observable<EntityResponseType> {
        return this.http.get<ISubject>(`${this.subjectResourceUrl}/${id}`, { observe: 'response' });
    }

    updateSubjectStatus(generalSubject: ISubject): Observable<EntityResponseType> {
        return this.http.put<ISubject>(this.subjectResourceUrl, generalSubject, { observe: 'response' });
    }

    delete(id: number): Observable<HttpResponse<any>> {
        return this.http.delete<any>(`${this.subjectResourceUrl}/${id}`, { observe: 'response' });
    }

    saveSubject(generalSubject: ISubject[]): Observable<EntityResponseType> {
        return this.http.post<ISubject>(this.subjectResourceUrlWithTree, generalSubject, { headers: headers, observe: 'response' });
    }

    getSubjectByCurriculumIdAndGradeId(curriculumId, gradeId, status): Observable<EntityArrayResponseType> {
        let reqJSON;
        if (status === ACTIVE) {
            reqJSON = {
                "curriculumId": curriculumId,
                "grade": gradeId,
                "status": status
            }
        } else {
            reqJSON = {
                "curriculumId": curriculumId,
                "grade": gradeId
            }
        }

        return this.http.post<ISubject[]>(this.subjectResourceUrlSearch, reqJSON, { observe: 'response' });
    }

    getSubjectTreeBySubjectId(id: number): Observable<EntityArrayResponseType> {
        return this.http.get<ISubject[]>(`${this.subjectResourceUrlWithTree}/${id}`, { observe: 'response' });
    }
}
